import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

export type ButtonIconHierarchy =
  | 'primary'
  | 'secondary'
  | 'tertiary'
  | 'quaternary';
export type ButtonIconSize = 'xs' | 'sm' | 'md' | 'lg';
export type ButtonIconType = 'button' | 'submit' | 'reset';

@Component({
  tag: 'bh-button-icon',
  styleUrl: 'bh-button-icon.css',
  shadow: true,
})
export class BhButtonIcon {
  /**
   * The visual hierarchy of the button
   */
  @Prop() hierarchy: ButtonIconHierarchy = 'primary';

  /**
   * The size of the button
   */
  @Prop() size: ButtonIconSize = 'md';

  /**
   * The icon name (Material Symbols)
   */
  @Prop() iconName: string = 'add';

  /**
   * Whether the button is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Whether the button is in loading state
   */
  @Prop() loading: boolean = false;

  /**
   * The type of button for form submission
   * - 'button': No form submission (default)
   * - 'submit': Submits the form
   * - 'reset': Resets the form
   */
  @Prop() type: ButtonIconType = 'button';

  /**
   * Accessible label for the button
   */
  @Prop() ariaLabel: string | null = null;

  /**
   * Emitted when the button is clicked
   */
  @Event() bhClick!: EventEmitter<MouseEvent>;

  private handleClick = (event: MouseEvent): void => {
    if (!this.disabled && !this.loading) {
      this.bhClick.emit(event);
    }
  };

  private handleKeyDown = (event: KeyboardEvent): void => {
    if (
      (event.key === 'Enter' || event.key === ' ') &&
      !this.disabled &&
      !this.loading
    ) {
      event.preventDefault();
      this.bhClick.emit(event as unknown as MouseEvent);
    }
  };

  render() {
    const buttonClasses = {
      'button-icon': true,
      [`button-icon-${this.hierarchy}`]: true,
      [`button-icon-${this.size}`]: true,
      'button-icon-disabled': this.disabled,
      'button-icon-loading': this.loading,
    };

    return (
      <button
        type={this.type}
        class={buttonClasses}
        disabled={this.disabled}
        aria-label={this.ariaLabel || this.iconName}
        aria-disabled={this.disabled ? 'true' : undefined}
        onClick={this.handleClick}
        onKeyDown={this.handleKeyDown}
        part="button"
      >
        {this.loading ? (
          <span class="loading-spinner" part="spinner"></span>
        ) : (
          <span class="material-symbols-outlined icon" part="icon">
            {this.iconName}
          </span>
        )}
      </button>
    );
  }
}
